<template>
  <v-container fluid fill-height style="padding:0;" id="array" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3>Array数组</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                  <table>
                    <colgroup>
                      <col span="2"> <!-- 不写span属性默认就是1 -->
                      <col span="1" style="min-width:300px;">
                      <col span="1" style="min-width:200px;">
                      <col span="1" style="min-width:100px;">
                      <col span="1" style="min-width:300px;">
                    </colgroup>
                    <tr>
                      <td></td>
                      <th>属性/方法名</th>
                      <th>测试数据</th>
                      <th>示例</th>
                      <th>输出</th>
                      <th>说明</th>
                      <th>备注</th>
                    </tr>
                    <tr>
                      <th>1</th>
                      <td id="length">length</td>
                      <td>arr = [1, 2];</td>
                      <td>arr.length = Math.pow(2,32)-1;</td>
                      <td>4294967295</td>
                      <td>数组的长度可以任意改变，如果超出数组实际元素个数，forEach遍历会忽略多余的空值</td>
                      <td>数组的长度最大值4294967295</td>
                    </tr>
                    <tr>
                      <th>2</th>
                      <td id="prototype">prototype</td>
                      <td>arr = [1, 2];</td>
                      <td>
                        <pre>{{pres[0]}}</pre>
                      </td>
                      <td>我是全局方法</td>
                      <td>prototype可以获取数组的原型对象，在原型对象上面定义全局的方法和属性</td>
                      <td></td>
                    </tr>
                    <tr>
                      <th rowspan="2">3</th>
                      <td rowspan="2" id="from">from</td>
                      <td>alike = 'abc';</td>
                      <td>Array.from(alike)</td>
                      <td>['a', 'b', 'c']</td>
                      <td>from方法是浅拷贝可以复制任何arraylike数组</td>
                      <td rowspan="2">ES6</td>
                    </tr>
                    <tr>
                      <td>
                          <pre>{{pres[1]}}</pre>
                      </td>
                      <td>alike(1, 2, 3)</td>
                      <td>[3, 6, 9]</td>
                      <td>
                        <ul>
                          <li>from第一个参数是类数组</li>
                          <li>from第二个参数是遍历的函数</li>
                          <li>from第三个参数是遍历函数中的this对象</li>
                        </ul>
                      </td>
                    </tr>
                    <tr>
                      <th>4</th>
                      <td id="isArray">isArray</td>
                      <td>arr = [1, 2];</td>
                      <td>Array.isArray(arr);</td>
                      <td>true</td>
                      <td>判断是否是数组，和Object.prototype.toString.call(arr) === '[object Array]';等效</td>
                      <td>ES5</td>
                    </tr>
                    <tr>
                      <th>5</th>
                      <td id="of">of</td>
                      <td colspan="2">Array.of('a', 1, true);</td>
                      <td>["a", 1, true]</td>
                      <td>根据传入的参数值创建数组</td>
                      <td>ES6</td>
                    </tr>
                    <tr>
                      <th>6</th>
                      <td id="concat">concat</td>
                      <td colspan="2"><pre>{{pres[2]}}</pre></td>
                      <td>[1, 2, 3, 4]</td>
                      <td>根据传入的数据，合并生成新的数组，其中合并操作只是浅拷贝</td>
                      <td>不影响原始数组</td>
                    </tr>
                    <tr>
                      <th>7</th>
                      <td id="copyWithin">copyWithin</td>
                      <td colspan="2"><pre>{{pres[3]}}</pre></td>
                      <td>["a", "b", "c", "d", "a"]</td>
                      <td>将数组的一部分复制到指定索引的位置
                        <ul>
                          <li>第一个参数，表示复制的目标索引，负数表示从末尾开始</li>
                          <li>第二个参数，表示要复制元素的开始索引</li>
                          <li>第三个参数，表示要复制元素的结束索引，不包括此索引指定的元素</li>
                        </ul>
                      </td>
                      <td>ES6,会修改原始数组</td>
                    </tr>
                    <tr>
                      <th rowspan="2">8</th>
                      <td rowspan="2" id="entries">entries</td>
                      <td rowspan="2">
                        const arr = ['a', 'b', 'c'];<br>
                        const ens = arr.entries();
                      </td>
                      <td><pre>{{pres[4]}}</pre></td>
                      <td rowspan="2">
                        [0, "a"]<br>
                        [1, "b"]<br>
                        [2, "c"]<br>
                      </td>
                      <td rowspan="2">生成一个数组迭代器对象，迭代器包含数组的键值对</td>
                      <td rowspan="2">ES6</td>
                    </tr>
                    <tr>
                      <td><pre>{{pres[5]}}</pre></td>
                    </tr>
                    <tr>
                      <th>9</th>
                      <td id="every">every</td>
                      <td>const arr = [4, 5, 6];</td>
                      <td><pre>{{pres[6]}}</pre></td>
                      <td>true</td>
                      <td>判断数组中的所有元素是否都满足条件，第二个可选参数表示迭代函数的this</td>
                      <td>ES5,任何空数组调用此方法都是true</td>
                    </tr>
                    <tr>
                      <th>10</th>
                      <td id="fill">fill</td>
                      <td colspan="2"><pre>{{pres[7]}}</pre></td>
                      <td>[1, 4, 4]</td>
                      <td>将指定元素填充到数组的指定位置
                        <ul>
                          <li>参数一，表示要填充的值</li>
                          <li>参数二，填充起始索引</li>
                          <li>参数三，填充结束索引，不包括此索引</li>
                        </ul>
                      </td>
                      <td>ES6,会修改原始数组</td>
                    </tr>
                    <tr>
                      <th>11</th>
                      <td id="filter">filter</td>
                      <td colspan="2"><pre>{{pres[8]}}</pre></td>
                      <td>[1, 8, 2]</td>
                      <td>过滤出满足条件的元素，第二个可选参数表示迭代函数的this</td>
                      <td>ES5,不会修改原始数组</td>
                    </tr>
                    <tr>
                      <th>12</th>
                      <td id="find">find</td>
                      <td colspan="2"><pre>{{pres[9]}}</pre></td>
                      <td>'c'</td>
                      <td>找出第一个满足条件的元素，第二个可选参数表示迭代函数的this，没有元素返回undefined</td>
                      <td>ES6</td>
                    </tr>
                    <tr>
                      <th>13</th>
                      <td id="findIndex">findIndex</td>
                      <td colspan="2"><pre>{{pres[10]}}</pre></td>
                      <td>2</td>
                      <td>找出第一个满足条件元素的索引，第二个可选参数表示迭代函数的this，没有元素返回-1</td>
                      <td>ES6</td>
                    </tr>
                    <tr>
                      <th rowspan="2">14</th>
                      <td rowspan="2" id="flat">flat</td>
                      <td rowspan="2">const arr = [1,2,[3, [4, 5]]];</td>
                      <td><pre>arr.flat();</pre></td>
                      <td>[1,2,3,[4,5]]</td>
                      <td rowspan="2">将嵌套的数组中的元素取到第一层来，传递参数表示嵌套的层数</td>
                      <td rowspan="2">IE系列不支持，不修改原始数组</td>
                    </tr>
                    <tr>
                      <td><pre>arr.flat(2);</pre></td>
                      <td>[1,2,3,4,5]</td>
                    </tr>
                    <tr>
                      <th>15</th>
                      <td id="flatMap">flatMap</td>
                      <td colspan="2"><pre>{{pres[11]}}</pre></td>
                      <td>["a", "b", "c", "d"]</td>
                      <td>用传入的迭代函数做一次map运算，然后再做一次flat运算，第二个可选参数表示迭代函数的this</td>
                      <td>IE系列不支持，不会修改原始数组</td>
                    </tr>
                    <tr>
                      <th>16</th>
                      <td id="forEach">forEach</td>
                      <td colspan="2"><pre>{{pres[12]}}</pre></td>
                      <td>
                        a<br>
                        b<br>
                        c<br>
                      </td>
                      <td>迭代元素，第二个可选参数表示迭代函数的this</td>
                      <td>ES5</td>
                    </tr>
                    <tr>
                      <th>17</th>
                      <td id="includes">includes</td>
                      <td colspan="2"><pre>['a', 'b', 'c'].includes('b', 1)</pre></td>
                      <td>true</td>
                      <td>判断元素是否包含在数组中，第二个可选参数表示查找的开始位置</td>
                      <td>ES7</td>
                    </tr>
                    <tr>
                      <th>18</th>
                      <td id="indexOf">indexOf</td>
                      <td colspan="2"><pre>['a', 'b', 'c'].indexOf('b', 1)</pre></td>
                      <td>1</td>
                      <td>查找第一个满足条件元素的索引，第二个可选参数表示查找的开始位置，没找到返回-1</td>
                      <td>ES5</td>
                    </tr>
                    <tr>
                      <th>19</th>
                      <td id="lastIndexOf">lastIndexOf</td>
                      <td colspan="2"><pre>['a', 'b', 'c'].lastIndexOf('b', 1)</pre></td>
                      <td>1</td>
                      <td>从后往前查找第一个满足条件元素的索引，第二个可选参数表示查找的开始位置，没找到返回-1</td>
                      <td>ES5</td>
                    </tr>
                    <tr>
                      <th>20</th>
                      <td id="join">join</td>
                      <td colspan="2"><pre>['a', 'b', 'c'].join('-')</pre></td>
                      <td>"a-b-c"</td>
                      <td>将数组拼接成字符串，参数可选，默认','</td>
                      <td></td>
                    </tr>
                    <tr>
                      <th>21</th>
                      <td id="keys">keys</td>
                      <td colspan="2"><pre>{{pres[13]}}</pre></td>
                      <td>
                        0<br>
                        1<br>
                        2<br>
                      </td>
                      <td>返回数组索引的可迭代对象</td>
                      <td>ES6</td>
                    </tr>
                    <tr>
                      <th>22</th>
                      <td id="values">values</td>
                      <td colspan="2"><pre>{{pres[14]}}</pre></td>
                      <td>
                        a<br>
                        b<br>
                        c<br>
                      </td>
                      <td>返回数组值的可迭代对象</td>
                      <td>ES6</td>
                    </tr>
                    <tr>
                      <th>23</th>
                      <td id="map">map</td>
                      <td colspan="2"><pre>{{pres[15]}}</pre></td>
                      <td>[3, 6, 9]</td>
                      <td>迭代元素产生新数组，第二个可选参数表示迭代函数的this</td>
                      <td>ES5,不修改原始数组</td>
                    </tr>
                    <tr>
                      <th>24</th>
                      <td id="pop">pop</td>
                      <td colspan="2"><pre>{{pres[16]}}</pre></td>
                      <td>['a', 'b']</td>
                      <td>删除数组最后一个元素，返回删除的元素</td>
                      <td>修改原始数组</td>
                    </tr>
                    <tr>
                      <th>25</th>
                      <td id="shift">shift</td>
                      <td colspan="2"><pre>{{pres[17]}}</pre></td>
                      <td>['b', 'c']</td>
                      <td>删除数组开头元素，返回删除的元素</td>
                      <td>修改原始数组</td>
                    </tr>
                    <tr>
                      <th>26</th>
                      <td id="push">push</td>
                      <td colspan="2"><pre>{{pres[18]}}</pre></td>
                      <td>["a", "b", "c", "d", "e"]</td>
                      <td>追加元素，返回元素的长度</td>
                      <td>修改原始数组</td>
                    </tr>
                    <tr>
                      <th>27</th>
                      <td id="unshift">unshift</td>
                      <td colspan="2"><pre>{{pres[19]}}</pre></td>
                      <td>["d", "e", "a", "b", "c"]</td>
                      <td>在数组的起始位置添加元素，返回元素的长度</td>
                      <td>修改原始数组</td>
                    </tr>
                    
                    <tr>
                      <th>28</th>
                      <td id="reduce">reduce</td>
                      <td colspan="2"><pre>{{pres[20]}}</pre></td>
                      <td>"result: a-b-c-"</td>
                      <td>累积运算，上一次计算的结果会作为下一次计算的第一个参数</td>
                      <td>ES5</td>
                    </tr>
                    <tr>
                      <th>29</th>
                      <td id="reduceRight">reduceRight</td>
                      <td colspan="2"><pre>{{pres[21]}}</pre></td>
                      <td>"result: c-b-a-"</td>
                      <td>从右往左累积运算，上一次计算的结果会作为下一次计算的第一个参数</td>
                      <td>ES5</td>
                    </tr>
                    <tr>
                      <th>30</th>
                      <td id="reverse">reverse</td>
                      <td colspan="2"><pre>['a', 'b', 'c'].reverse();</pre></td>
                      <td>["c", "b", "a"]</td>
                      <td>反转数组</td>
                      <td>会修改原始数组</td>
                    </tr>
                    <tr>
                      <th rowspan="2">31</th>
                      <td rowspan="2" id="slice">slice</td>
                      <td rowspan="2">const arr = ['a', 'b', 'c'];</td>
                      <td><pre>arr.slice(1)</pre></td>
                      <td>["b", "c"]</td>
                      <td rowspan="2">截取数组中的一部分
                        <ul>
                          <li>参数一，表示截取的起始索引</li>
                          <li>参数二，表示截取的结束索引，不包含</li>
                        </ul>
                      </td>
                      <td rowspan="2">不会修改原始数组</td>
                    </tr>
                    <tr>
                      <td><pre>arr.slice(-2, -1);</pre></td>
                      <td>["b"]</td>
                    </tr>
                    <tr>
                      <th>32</th>
                      <td id="some">some</td>
                      <td colspan="2"><pre>{{pres[22]}}</pre></td>
                      <td>true</td>
                      <td>判断数组中是否有一个满足条件的元素，第二个可选参数表示迭代函数的this</td>
                      <td>ES5,不修改原始数组</td>
                    </tr>
                    <tr>
                      <th rowspan="2">33</th>
                      <td rowspan="2" id="sort">sort</td>
                      <td rowspan="2">const arr = [0.1, 1, 51, 8, 50, 2];</td>
                      <td><pre>arr.sort((a, b) => a - b);</pre></td>
                      <td>[0.1, 1, 2, 8, 50, 51]</td>
                      <td>升序排序</td>
                      <td rowspan="2">会修改原始数组</td>
                    </tr>
                    <tr>
                      <td><pre>arr.sort((a, b) => b - a);</pre></td>
                      <td>[51, 50, 8, 2, 1, 0.1]</td>
                      <td>降序排序</td>
                    </tr>
                    <tr>
                      <th rowspan="2">34</th>
                      <td rowspan="2" id="splice">splice</td>
                      <td rowspan="2">const arr = ['a', 'b', 'c'];</td>
                      <td><pre>arr.splice(0, 1);</pre></td>
                      <td>["b", "c"]</td>
                      <td>删除元素
                        <ul>
                          <li>参数一，删除的起始位置</li>
                          <li>参数二，删除的个数</li>
                        </ul></td>
                      <td rowspan="2">会修改原始数组</td>
                    </tr>
                    <tr>
                      <td><pre>arr.splice(2, 0, 'd', 'e');</pre></td>
                      <td>["a", "b", "d", "e", "c"]</td>
                      <td>删除元素然后添加元素</td>
                    </tr>
                    <tr>
                      <th>35</th>
                      <td id="toLocaleString">toLocaleString</td>
                      <td colspan="2"><pre>{{pres[23]}}</pre></td>
                      <td>"a,￥10.00,2019/5/27 17:59:18"</td>
                      <td>将数组按照一定格式转化成字符串
                        <ul>
                          <li>参数一，表示语言格式</li>
                          <li>参数二，表示对数组中的不同数据类型的处理配置</li>
                        </ul>
                      </td>
                      <td></td>
                    </tr>
                    <tr>
                      <th>36</th>
                      <td id="toString">toString</td>
                      <td colspan="2"><pre>['a', 10, new Date()].toString();</pre></td>
                      <td>"a,10,Mon May 27 2019 18:10:24 GMT+0800 (中国标准时间)"</td>
                      <td>将数组转化成字符串</td>
                      <td></td>
                    </tr>
                    <tr>
                      <th>37</th>
                      <td id="Symbol-iterator">Symbol.iterator</td>
                      <td colspan="2"><pre>{{pres[24]}}</pre></td>
                      <td>
                        a<br>
                        b<br>
                        c
                      </td>
                      <td>获取迭代器, ES6新增的特性，其实就是键名</td>
                      <td>ES6</td>
                    </tr>
                  </table>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
  `
  Array.prototype.fn = () => {
    console.log("我是全局方法");}
  arr.fn();
  `,
  `
  function alike() {
    return Array.from(
      arguments, 
      function(value, item) {
        return value * this.param;
      }, 
      {param: 3}
    );
  }
  `,
  `
  [].concat(1,2,3,4)
  [].concat([1,2], [3,4])
  `,
  `
  ['a', 'b', 'c', 'd', 'e'].copyWithin(-1);
  ['a', 'b', 'c', 'd', 'e'].copyWithin(-1, 0, 1);
  ['a', 'b', 'c', 'd', 'e'].copyWithin(4, 0);
  ['a', 'b', 'c', 'd', 'e'].copyWithin(4, 0, 1);
  `,
  `
  for(const values of ens) {
    console.log(values);
  }
  `,
  `
  let end = ens.next();
  while(!end.done){
    console.log(end.value);
    end = ens.next();
  }
  `,
  `
  arr.every(function(item, index, arr){
    return item >= this.param;
  }, {param: 4})
  `,
  `
  [1,2,3].fill(4, 1, 3);
  [1,2,3].fill(4, 1);
  `,
  `
  const arr = [0.1, 1, 51, 8, 50, 2].filter(function(item, index, array){
    return item >= this.min && item <= this.max
  }, {
    min: 1,
    max: 10
  })
  `,
  `
  const arr = ['a', 'b', 'c'];
  const res = arr.find(function(item, index, array){
    return item > this.param;
  }, {
    param: 'b'
  })
  `,
  `
  const arr = ['a', 'b', 'c'];
  const res = arr.findIndex(function(item, index, array){
    return item > this.param;
  }, {
    param: 'b'
  })
  `,
  `
  const arr = ["a b", "c d"];
  arr.flatMap(function(item, index, array){
    return item.split(this.param);
  }, {param: " "})
  `,
  `
  ['a', 'b', 'c'].forEach(function(item, index, array){
    console.log(item);
  })
  `,
  `
  for(var key of ['a', 'b', 'c'].keys()){
    console.log(key);
  }
  `,
  `
  for(var key of ['a', 'b', 'c'].values()){
    console.log(key);
  }
  `,
  `
  const arr = [1, 2, 3].map(function(item, index, array){
    return item * this.param;
  }, {param: 3});
  `,
  `
  const arr = ['a', 'b', 'c'];
  arr.pop();
  `,
  `
  const arr = ['a', 'b', 'c'];
  arr.shift();
  `,
  `
  const arr = ['a', 'b', 'c'];
  arr.push('d', 'e');
  `,
  `
  const arr = ['a', 'b', 'c'];
  arr.unshift('d', 'e');
  `,
  `
  const res = ['a', 'b', 'c'].reduce(function(pre, cur, index, array) {
    return pre = pre + cur + "-";
  }, "result: ");
  `,
  `
  const res = ['a', 'b', 'c'].reduceRight(function(pre, cur, index, array) {
    return pre = pre + cur + "-";
  }, "result: ");
  `,
  `
  const arr = ['a', 'b', 'c'];
  arr.some(function(item, index, array){
    return item > this.param;
  }, {param: 'b'})
  `,
  `
  const arr = ['a', 10, new Date()];
  arr.toLocaleString('zh-Hans-CN', {
    timeZone: "Asia/Shanghai", 
    hourCycle: "h24", 
    currency: "CNY", 
    style: "currency"
  });
  `,
  `
  for(var key of ['a', 'b', 'c'][Symbol.iterator]()){
    console.log(key);
  }
  `
],
      time: "2018-12-01",
      title: "Array数组",
      items: [
        {
          title: "length",
          target: "#length"
        },
        { divider: true },
        {
          title: "prototype",
          target: "#prototype"
        },
        { divider: true },
        {
          title: "from",
          target: "#from"
        },
        { divider: true },
        {
          title: "isArray",
          target: "#isArray"
        },
        { divider: true },
        {
          title: "of",
          target: "#of"
        },
        { divider: true },
        {
          title: "concat",
          target: "#concat"
        },
        { divider: true },
        {
          title: "copyWithin",
          target: "#copyWithin"
        },
        { divider: true },
        {
          title: "entries",
          target: "#entries"
        },
        { divider: true },
        {
          title: "every",
          target: "#every"
        },
        { divider: true },
        {
          title: "fill",
          target: "#fill"
        },
        { divider: true },
        {
          title: "filter",
          target: "#filter"
        },
        { divider: true },
        {
          title: "find",
          target: "#find"
        },
        { divider: true },
        {
          title: "findIndex",
